import Vue from 'vue'
// button
import Button from './button'
import Icon from './icon'
import ButtonGroup from './button-group'
// input
import Input from './input'
// grid
import Row from './row'
import Col from './col'
//  button 
import chai from 'chai'
import spies from 'chai-spies'


/**
 * 1.  loadingStatus1,loadingStatus2 newVuedatadata
 * 2. 
 */
Vue.component('g-button', Button)
Vue.component('g-icon', Icon)
Vue.component('g-button-group', ButtonGroup)
Vue.component('g-input', Input)
Vue.component('g-row', Row)
Vue.component('g-col', Col)

// new Vue 
new Vue({
    el: "#app",
    data: {
        loadingStatus1: false,
        loadingStatus2: false,
        message:'hi'
    },
    methods: {
        inputChange(e) {
            console.log(e.target.value)
        }
    }
})

chai.use(spies)
const expect = chai.expect

// 
// props
try {
    //  icon
    {
        const Constructor = Vue.extend(Button)
        //  vue 
        const vm = new Constructor({
            propsData: {
                icon: 'settings'
            }
        })

        vm.$mount()
        let useElement = vm.$el.querySelector('use')
        let href = useElement.getAttribute('xlink:href')
        expect(href).to.eq('#i-settings')
        // vue
        vm.$el.remove()
        // 
        vm.$destroy()
    }
    //  loading
    {
        const Constructor = Vue.extend(Button)
        //  vue 
        const vm = new Constructor({
            propsData: {
                icon: 'settings',
                loading: true
            }
        })

        vm.$mount() //  button 
        let useElement = vm.$el.querySelector('use')
        let href = useElement.getAttribute('xlink:href')
        expect(href).to.eq('#i-loading')
        vm.$destroy()
    }
    // svgorder
    {
        // document css 
        const div = document.createElement('div')
        document.body.appendChild(div)
        const Constructor = Vue.extend(Button)
        //  vue 
        const vm = new Constructor({
            propsData: {
                icon: 'settings',
            }
        })
        //  button  div 
        vm.$mount(div)
        let svg = vm.$el.querySelector('svg') //  button  svg
        let {order} = window.getComputedStyle(svg) //  svg 
        expect(order).to.eq('1') //  .icon  .content 
        vm.$el.remove()
        vm.$destroy()
    }
    //  icon-position
    {
        //  vue document  div#test  div#test 
        const div = document.getElementById('test')
        const Constructor = Vue.extend(Button)
        const vm = new Constructor({
            propsData: {
                icon: 'settings',
                iconPosition: 'right'
            }
        })
        vm.$mount(div)
        let svg = vm.$el.querySelector('svg')
        let {order} = window.getComputedStyle(svg)
        expect(order).to.eq('2')

        vm.$el.remove()
        vm.$destroy()
    }

    //  button  click  vuetouch
    {
        const Constructor = Vue.extend(Button)
        const vm = new Constructor({
            propsData: {
                icon: 'settings',
                iconPosition: 'right'
            }
        })
        vm.$mount()
        let spy = chai.spy(function () {
        })
        //  touch spy
        vm.$on('touch', spy)
        //  vm vuebutton DOM
        let button = vm.$el
        //  button  click 
        button.click()
        //  button  click spy 
        expect(spy).to.have.been.called()

        vm.$el.remove()
        vm.$destroy()
    }
} catch (e) {

}



